/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem; }

p {
  color: #FFFFFF;
  font-size: 2rem; }

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto; }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #0da6f3;
  text-align: center; }

h1 {
  font-size: 4rem; }

h2 {
  font-size: 3.6rem; }

h3 {
  font-size: 3rem;
  text-align: center; }

body {
  background-color: #1a1b15; }

@media (min-width: 768px) {
  .container-esthetic {
    height: 100vh;
    display: grid;
    gap: 2.5rem;
    grid-template-columns: repeat(2, 1fr); } }

.container-esthetic .image {
  height: 30rem;
  background-image: url("/build/img/1.jpg");
  background-size: cover;
  background-position: center; }
  @media (min-width: 768px) {
    .container-esthetic .image {
      height: auto; } }

.app {
  width: 95%;
  margin: 0 auto;
  padding-top: 2.5rem; }
  .app .section {
    display: none;
    margin-top: 2.5rem; }

.show-section {
  display: block !important; }

.tabs {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(3, 1fr); }
  .tabs button {
    background-color: #FFFFFF;
    font-size: 1.4rem;
    font-weight: 700;
    padding: 1rem;
    text-align: center;
    text-transform: uppercase;
    border: none;
    outline: none; }
    .tabs button.current {
      background-color: #0da6f3;
      color: white; }
    .tabs button:hover {
      cursor: pointer; }

@media (min-width: 768px) {
  .list-of-services {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr); } }

.list-of-services .service {
  margin-bottom: 2rem;
  background-color: #FFFFFF;
  padding: 2.5rem;
  border-radius: 1rem; }
  @media (min-width: 768px) {
    .list-of-services .service {
      margin-bottom: 0; } }
  .list-of-services .service p {
    margin: 0 0 1.25rem 0; }
  .list-of-services .service .service-name {
    color: #1a1b15; }
  .list-of-services .service .service-price {
    color: #0da6f3;
    margin: 0;
    font-size: 3rem;
    font-weight: 700; }
  .list-of-services .service.selected {
    background-color: #0da6f3; }
    .list-of-services .service.selected p {
      color: #FFFFFF; }
  .list-of-services .service:hover {
    cursor: pointer; }

.pagination {
  display: flex;
  justify-content: space-between;
  padding: 5rem 0; }
  .pagination button {
    background-color: #0da6f3;
    padding: 1rem 3rem;
    display: block;
    text-align: center;
    color: #FFFFFF;
    border: none;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 2;
    outline: none; }
    .pagination button.hide {
      visibility: hidden; }
    .pagination button:hover {
      cursor: pointer; }

.form {
  width: 100%; }

.field {
  display: flex;
  align-items: center;
  margin-bottom: 2rem; }
  .field label {
    flex: 0 0 10rem;
    color: #FFFFFF; }
  .field input {
    flex: 1;
    padding: 1.25rem;
    border-radius: 1rem;
    border: none;
    outline: none; }

.alert {
  padding: 2.5rem;
  text-align: center;
  color: #FFFFFF;
  width: 100%;
  display: block; }
  .alert.error {
    background-color: #c20000; }

.invalid-appointment {
  text-align: center;
  margin: 2.5rem 0;
  text-transform: uppercase; }

.summary-content {
  margin-bottom: 2.5rem 0; }
  .summary-content p {
    margin-bottom: .5rem; }
    .summary-content p span {
      font-weight: 700;
      color: #0da6f3;
      font-size: 2.4rem; }
  .summary-content .summmary-services h3 {
    margin: 2.5rem 0; }
  .summary-content .summmary-services .container-service {
    margin-bottom: 2rem;
    padding-bottom: 2rem;
    border-bottom: 1px solid #43454C; }
    .summary-content .summmary-services .container-service .price {
      font-size: 2.4rem;
      font-weight: bold;
      color: #0da6f3; }
